.about {
  padding-top: 15rem;
  overflow: hidden;
}
.about p {
  font-size: 1.6rem;
  color: whitesmoke;
  text-align: left;
  margin: 0 5rem 2rem 5rem;
  line-height: 3.2rem;
}
#greet {
  color: #c770f0;
}

.about a {
  color: #c770f0;
  text-decoration: none;
  display: inline-block;
}
.about a::after {
  content: "";
  display: block;
  width: 0;
  height: 3px;
  background-color: #c770f0;
  transition: width 0.3s ease-in-out;
}
.about a:hover::after {
  width: 100%;
}

@media screen and (max-width: 1200px) {
  .about {
    padding-top: 12rem;
    overflow: scroll;
  }
  .about p {
    font-size: 1.2rem;
    text-align: justify;
    margin: 0 2rem 2rem 3rem;
    line-height: 2rem;
  }
}
